.home {
	display: flex;
	position: relative;
	min-height: 100dvh;
	align-items: center;
	justify-content: center;
}

.home__first {
	text-align: center;
	margin-bottom: 5rem;
}

.home__logo {
	width: 155px;
	height: 155px;
	cursor: pointer;
}

.home__name {
	font-size: var(--h1-font-size);
	font-weight: var(--font-bold);
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
}

.home__letter, .home__down {
	transition: all .15s ease-in-out;
}

.home__down:hover {
	color: var(--first-color);
}

.home__letter:hover {
	transform: translateY(-3.5px);
	color: var(--first-color);
}

.home__space {
	margin-right: 0.75rem;
}

.home__second {
	display: flex;
	column-gap: 1rem;
	align-items: center;
	justify-content: center;
	margin-bottom: 1rem;
	margin-top: 1rem;
}

.home__icon {
	width: 30px;
	height: 30px;
}

.circle-container-home {
	width: 50px;
	height: 50px;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--container-color);
}

figure {
	margin-bottom: 1rem;
	transition: all .1s ease-in-out;
}

figure:hover {
	transform: scale(1.1);
}

figure:active {
	transform: scale(0.9);
}

.home__greet {
	position: absolute;
	height: 90px;
}

.home__link {
	transition: all .15s ease-in-out;
}

.home__link:hover {
	transform: scale(1.2);
}

.home__link:active {
	transform: scale(0.85);
}

.home__down {
	margin-top: 7rem;
}

.down {
	animation-duration: 1.5s;
}

@media screen and (max-width: 520px) {
	.home__first {
		margin-bottom: 6rem;
	}

	.home__logo {
		width: 120px;
		height: 120px;
	}
	
	.home__name {
		font-size: var(--h2-font-size);
	}

	.circle-container-home {
		width: 45px;
		height: 45px;
	}

	.home__icon {
		width: 25px;
		height: 25px;
	}

	.home__greet {
		height: 55px;
	}
}

@media screen and (max-width: 350px) {
	.home__first {
		margin-bottom: 6rem;
	}

	.home__logo {
		width: 75px;
		height: 75px;
	}
	
	.home__name {
		font-size: var(--h3-font-size);
	}

	.circle-container-home {
		width: 37px;
		height: 37px;
	}

	.home__icon {
		width: 20px;
		height:20px;
	}

	.home__greet {
		height: 40px;
	}
}
